/** Replace hero video with GIF */

.hero-gif__container {
	display: none;
}

@media(max-width: 640px) {
	.hero-gif__container {
		display: block;
		width: 100%;
		margin-top: 2em;
	}
	.hero-gif__sec {
		padding: 0.8em 1.5em;
		background-color: #F7EEE3;
		color: black;
		text-align: center
	}
	.hero-gif__wrapper {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.hero-gif__wrapper .hero-gif__text {
		text-align: left;
		letter-spacing: 1px;
		max-width: 50%
	}
	#hero-gif__head {
		margin: 0;
	}
	.hero-gif__button {
		font-size: 0.6em;
		font-weight: 700;
		letter-spacing: 1px;
		margin-left: 16px;
		padding: 0.6em 2.5em;
		background-color: Transparent;
		background-repeat: no-repeat;
		border: 0.233em solid;
		cursor: pointer;
		overflow: hidden;
		outline: none;
		color: black;
	}
}
/** end of hero GIF css **/